.swipeButtons {
    display: flex;
    bottom: 2vh;
    position: fixed;
    width: 100%;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root {
    background-color: white;
    box-shadow: 0 10px 53px 0 rgba(0, 0, 0, 0.3);
}

.swipeButtons_repeat,.swipeButtons_left,.swipeButtons_star,.swipeButtons_right,.swipeButtons_lightning {
    padding: 1vw !important;
}

.swipeButtons_repeat {
    color: rgb(245, 207, 35) !important;
}

.swipeButtons_right {
    color: rgb(255, 122, 60) !important;
}

.swipeButtons_lightning {
    color: rgb(72, 142, 233) !important;
}

.swipeButtons_star {
    color: springgreen !important;
}

.swipeButtons_left {
    color: #fc4866 !important;
}