@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
* {
  margin: 0;

}

body {
  margin: 0;
  font-family:  -apple-system, 'Open Sans', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 100;
    border-bottom: 1px solid #f9f9f9;
}

.header__logo {
    object-fit: contain;
    height: 40px;
}

.header__icon {
    padding: 20px;
}

.card {
    position: relative;
    background-color: #fff;
    width: 400px;
    padding: 20px;
    max-width: 85vw;
    height: 60vh;
    box-shadow: 0px 10px 40px 0px rgba(221, 221, 221, 0.123);
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}

.tinderCards__cardContainer {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
}

.swipe {
    position: absolute;
}

.cardContent {
    width: 100%;
    height: 100%;
}

.card h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: #fff;
}

.swipeButtons {
    display: flex;
    bottom: 2vh;
    position: fixed;
    width: 100%;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root {
    background-color: white;
    box-shadow: 0 10px 53px 0 rgba(0, 0, 0, 0.3);
}

.swipeButtons_repeat,.swipeButtons_left,.swipeButtons_star,.swipeButtons_right,.swipeButtons_lightning {
    padding: 1vw !important;
}

.swipeButtons_repeat {
    color: rgb(245, 207, 35) !important;
}

.swipeButtons_right {
    color: rgb(255, 122, 60) !important;
}

.swipeButtons_lightning {
    color: rgb(72, 142, 233) !important;
}

.swipeButtons_star {
    color: springgreen !important;
}

.swipeButtons_left {
    color: #fc4866 !important;
}
